<script>
  // Actual-Url = https://official-joke-api.appspot.com
  import Joke from "./joke.svelte";
  import { numTextStore } from "../stores/store.js";
  import { catTextStore } from "../stores/store.js";
  // export let jokes;
  let numtext;
  let catText;

  numTextStore.subscribe(value => {
    numtext = value;
  });

  catTextStore.subscribe(value => {
    catText = value;
  });

  let jokes;
  $: url = `http://localhost:3005/jokes/${catText}/${numtext}`;

  $: getJokes = async () => {
    const response = await fetch(url);
    const data = await response.json();
    jokes = data;
    return data;
  };
</script>

<style>
  button {
    background-color: orange;
    color: blue;
    padding: 10px;
    font-size: 2em;
    border-radius: 1em;
    outline-style: none;
  }
  button:active {
    outline-style: none;
  }
</style>

<div class="get-BUtton">
  <button on:click={getJokes}>Get Jokes</button>
</div>
{#if jokes}
  <div class="jokes-container">
    {#each jokes as joke}
      <Joke {joke} />
    {/each}
  </div>
{/if}
